import React from 'react';
import {Row, Col, Card, Table, Container, ToggleButtonGroup, ToggleButton } from 'react-bootstrap';
import { NavLink, Redirect } from 'react-router-dom';

import Aux from "../../hoc/_Aux";
import { RangeEarningsChart, } from "../Charts/Nvd3Chart/EarningsChart";
import { CaseBrowseTable } from '../Custom/CaseExplorer';

import logo from '../../assets/images/leginar_logo.png';
import '../../App/layout/AdminLayout/app.scss';

class Dashboard extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            redirect: false,
            redirect_to: undefined,
            chart_view: 0,
        }

        this.setRedirect = this.setRedirect.bind(this);
    }

    setRedirect(route) {
        this.setState({ redirect: true, redirect_to: route });
    }

    render() {
        if (this.state.redirect) {
            return <Redirect push to={this.state.redirect_to} />
        }
        return (
            <Aux>
                <Container className="py-5">
                    <Row className="align-items-baseline">
                        <NavLink to="/dashboard"><img src={logo} alt="Leginar" /></NavLink>
                        <h1 className="display-4">- Investment Dashboard</h1>
                    </Row>
                    
                    <Row>
                        <Col xl={9} lg={10} md={10} sm={12} xs={12}>
                            {/* Begin Earnings Chart */}
                            <Row>
                                <Col>
                                    <Card>
                                        <Card.Body>
                                            <Row>
                                                <h6 className="mb-3">Projected Earnings</h6>
                                            </Row>
                                            <Row>
                                                <RangeEarningsChart view={this.state.chart_view} />
                                            </Row>

                                            <Row className="justify-content-center">
                                                <ToggleButtonGroup type="radio" name="chart-view" defaultValue={0} onChange={value => this.setState({chart_view: value})}>
                                                    <ToggleButton value={0}>Y</ToggleButton>
                                                    <ToggleButton value={1}>All</ToggleButton>
                                                    <ToggleButton value={2}>D</ToggleButton>
                                                </ToggleButtonGroup>
                                            </Row>
                                        </Card.Body>
                                    </Card>
                                </Col>
                            </Row>
                            {/* End Earnings Chart */}

                            <Row>
                                {/* Begin Case Feed */}
                                <Col xl={12} lg={12} md={12} sm={12} xs={12}>
                                    <Card>
                                        <Card.Header>
                                            <Card.Title as='h5'>Your Cases</Card.Title>
                                        </Card.Header>
                                        <Card.Body className='px-0 py-2'>
                                            <Table responsive hover>
                                                <CaseBrowseTable redirect={this.setRedirect} />
                                            </Table>
                                        </Card.Body>
                                    </Card>
                                </Col>
                                {/* Begin Case Feed */}
                            </Row>


                            <Row>
                                <Col xl={6} lg={6} md={6} sm={6} xs={6}>
                                    {/* Begin Upcoming Deadline */}
                                    <Card className='card-event'>
                                        <Card.Body>
                                            <div className="row align-items-center justify-content-center mb-4">
                                                <div className="col">
                                                    <h5 className="m-0">Upcoming Deadline</h5>
                                                </div>
                                                <div className="col-auto">
                                                    <label className="label theme-bg2 text-white f-14 f-w-400 float-right">Invested $100</label>
                                                </div>
                                            </div>
                                            <h2 className="mt-2 f-20">Doe v. Evil Incorporated</h2>
                                            <h6 className="text-muted mt-3 mb-0">Submit request for Court of Appeals</h6>
                                            <i className="fa fa-gavel mt-2 text-c-purple f-50" style={{color: '#A8D0E6' }}/>
                                        </Card.Body>
                                    </Card>
                                    {/* End Upcoming Deadline */}
                                </Col>

                                <Col xl={6} lg={6} md={6} sm={6} xs={6}>
                                    {/* Begin Last Submission */}
                                    <Card className='card-event'>
                                        <Card.Body>
                                            <div className="row align-items-center justify-content-center mb-4">
                                                <div className="col">
                                                    <h5 className="m-0">Last Submission</h5>
                                                </div>
                                            </div>
                                            <h2 className="mt-2 f-20">Jane v. Slander Media Co.</h2>
                                            <h6 className="text-muted mt-3 mb-0 col w-75">Submitted case brief to be overlooked by US District Court</h6>
                                            <i className="fa fa-gavel mt-2 f-50 col-auto" style={{color: '#F76C6C'}} />
                                        </Card.Body>
                                    </Card>
                                    {/* End Last Submission */}

                                </Col>
                            </Row>
                        </Col>
                        {/* Trending Cases */}
                        <Col>
                            <Card>
                                <Card.Header>
                                    <Card.Title as="h5">Trending Cases</Card.Title>
                                </Card.Header>
                                <Card.Body>
                                    <ul>
                                        <li>Marbury v. Madison</li>
                                        <li>Angular v. React</li>
                                        <li>Coke v. Pepsi</li>
                                        <li>Unix v. MSDOS</li>
                                        <li>Functional v. Object Oriented</li>
                                    </ul>
                                </Card.Body>
                            </Card>
                        </Col>
                    </Row>
                </Container>
            </Aux>
        );
    }
}

export default Dashboard;