const EnumViews = {
    YEAR: 0,
    TOTAL: 1,
    MONTH:2,
};

export {EnumViews};

export function RangeEarningsChart(props) {

    const { view } = props;

    const data = view === EnumViews.YEAR ? getDatum() :
                    view === EnumViews.TOTAL ? getDatum2() :
                    view === EnumViews.MONTH ? getDatum3():
                    getDatum();

    const indexView = ['Month', 'Year', 'Day'];

    const map_labels = [
        (
            d => {
                const month_map = ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec'];
                return month_map[d];
            }
        ),
        ( d => d ),
        ( d => d ),
    ];
    
    
    return (
        <div>
            {
                React.createElement(NVD3Chart, {
                    xAxis: {
                        tickFormat: map_labels[view],
                        axisLabel: indexView[view],
                    },
                    yAxis: {
                        axisLabel: 'Earnings ($)',
                        tickFormat: function(d) {return parseFloat(d).toFixed(0); }
                    },
                    type:'lineChart',
                    datum: data,
                    x: 'x',
                    y: 'y',
                    height: 250,
                    renderEnd: function(){
                        console.log('renderEnd');
                    }
                })
            }
        </div>
    );
}